import React, { Component } from 'react';
import axios from 'axios';

import StockForm from './StockForm';

export default class StockEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
        if(this.props.location.state && this.props.location.state.stock){
            this.setState({data: this.props.location.state.stock})
        }else if(this.props.match.params.itemId){
            axios.get(`/api/stocks/${this.props.match.params.itemId}`).then(response => {
                this.setState({data: response.data});
            },error=>this.setState({error:error.response.data}))
        }
        
    }

  submit = (data) => {
        return axios.put(`/api/stocks/${this.props.match.params.itemId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push(this.props.closeUrl);
    }

    render() {
        return <StockForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}