import React, { Component } from 'react';
import axios from 'axios';

import StockForm from './StockForm';

export default class StockNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post(this.props.url, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push(this.props.closeUrl);
    }
    render() {
        return <StockForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}