import React, { Component } from 'react';
import { Route } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import StockPagesKitchen from './StockKitchen';
import StockPagesLaboratory from './StockLaboratory';
import StockPagesGeneral from './StockGeneral';

export default class StockPages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
						<Route path={`${match.path}/kitchen`} render={(props) => <StockPagesKitchen {...props} /> } />
						<Route path={`${match.path}/laboratory`} render={(props) => <StockPagesLaboratory {...props} /> } />
						<Route path={`${match.path}/general`} render={(props) => <StockPagesGeneral {...props} /> } />
						<Route path=""><h1>404 Not Found</h1></Route>
					</AnimatedSwitch>
	}
}
