import React, { Component } from 'react';
import axios from 'axios';

import StockQuantityForm from './StockQuantityForm';

export default class StockAdditionNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post("/api/stocks/addition", data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push(this.props.closeUrl);
    }
    render() {
        return <StockQuantityForm itemsUrl={this.props.itemsUrl} type="Addition" error={this.state.error} submit={this.submit} close={this.close} title={this.props.title} />
    }
}
