import React, { Component } from 'react';
import axios from 'axios';

import StockQuantityForm from './StockQuantityForm';

export default class StockAdditionEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
        if(this.props.location.state && this.props.location.state.addition){
            this.setState({data: this.props.location.state.addition})
        }else if(this.props.match.params.additionId){
            axios.get(`/api/stocks/addition/${this.props.match.params.additionId}`).then(response => {
                this.setState({data: response.data});
            },error=>this.setState({error:error.response.data}))
        }
        
    }

    submit = (data) => {
        return axios.put(`/api/stocks/addition/${this.props.match.params.additionId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push(this.props.closeUrl);
    }

    render() {
        let deleteUrl = `/api/stocks/addition/${this.props.match.params.additionId}`
        return <StockQuantityForm itemsUrl={this.props.itemsUrl} deleteUrl={deleteUrl} type="Addition" edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} title={this.props.title} />
    }
}
