import React, { Component } from 'react';
import { Route, Link } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import StockItems from '../components/StockItems';
import StockNew from '../components/StockNew';
import StockEdit from '../components/StockEdit';
import StockUsage from '../components/StockUsage';
import StockUsageNew from '../components/StockUsageNew';
import StockUsageEdit from '../components/StockUsageEdit';
import StockAddition from '../components/StockAddition';
import StockAdditionNew from '../components/StockAdditionNew';
import StockAdditionEdit from '../components/StockAdditionEdit';

export default class StockPagesGeneral extends Component {
    render() {
        let match = this.props.match;
        return <div className="container-fluid">
            <div className="row">
                <div className="col-md-12">
                  <span className="pull-right">
                  <Link to={`${match.url}/addition`} className="btn btn-secondary details ml-1">View Additions</Link>
                  <Link to={`${match.url}/usage`} className="btn btn-warning details ml-1">View Usage</Link>
                  <Link to={`${match.url}/addition/new`} className="btn btn-primary details ml-1">Enter Addition</Link>
                  <Link to={`${match.url}/usage/new`} className="btn btn-info details ml-1">Enter Usage</Link>
                    <Link to={`${match.url}/new`} className="btn btn-success details ml-1">Add Item</Link>
                    <Link to={`${match.url}`} className="btn btn-dark details ml-1">General Stock</Link>
                  </span>
                </div>
            </div>
            <AnimatedSwitch debug={true}>
                        <Route path={`${match.path}/new`} render={(props) => <StockNewGeneral {...props} /> } />
                        <Route path={`${match.path}/usage/new`} render={(props) => <StockUsageNewGeneral {...props} /> } />
                        <Route path={`${match.path}/usage/:usageId`} render={(props) => <StockUsageEditGeneral {...props} /> } />
                        <Route path={`${match.path}/usage`} render={(props) => <StockUsageGeneral {...props} /> } />
                        <Route path={`${match.path}/addition/new`} render={(props) => <StockAdditionNewGeneral {...props} /> } />
                        <Route path={`${match.path}/addition/:additionId`} render={(props) => <StockAdditionEditGeneral {...props} /> } />
                        <Route path={`${match.path}/addition`} render={(props) => <StockAdditionGeneral {...props} /> } />
                        <Route path={`${match.path}/:itemId`} render={(props) => <StockEditGeneral {...props} /> } />
                        <Route path={`${match.path}`} render={(props) => <StockGeneral {...props} /> } />
                        <Route path=""><h1>404 Not Found</h1></Route>
                    </AnimatedSwitch>
          </div>
    }
}

export class StockGeneral extends Component {
    render() {
        return <StockItems title="General Stock" url="/api/stocks/general" match={this.props.match} history={this.props.history} />
    }
}

export class StockNewGeneral extends Component {
    render() {
        return <StockNew url="/api/stocks/general" closeUrl="/school/stocks/general" history={this.props.history} />
    }
}

export class StockEditGeneral extends Component {
    render() {
        return <StockEdit closeUrl="/school/stocks/general" match={this.props.match} history={this.props.history} location={this.props.location} />
    }
}

export class StockUsageGeneral extends Component {
    render() {
        return <StockUsage url="/api/stocks/general/usage" title="General Usage" match={this.props.match} history={this.props.history} />
    }
}


export class StockUsageNewGeneral extends Component {
    render() {
        return <StockUsageNew title="Usage Details" itemsUrl="/api/stocks/general" closeUrl="/school/stocks/general" history={this.props.history} />
    }
}

export class StockUsageEditGeneral extends Component {
    render() {
        return <StockUsageEdit title="Usage Details" itemsUrl="/api/stocks/general" closeUrl="/school/stocks/general/usage" match={this.props.match} history={this.props.history} location={this.props.history} />
    }
}

export class StockAdditionGeneral extends Component {
    render() {
        return <StockAddition url="/api/stocks/general/addition" title="General Addition" match={this.props.match} history={this.props.history} />
    }
}

export class StockAdditionNewGeneral extends Component {
    render() { 
        return <StockAdditionNew title="Addition Details" itemsUrl="/api/stocks/general" closeUrl="/school/stocks/general" history={this.props.history} />
    }
}


export class StockAdditionEditGeneral extends Component {
    render() {
        return <StockAdditionEdit title="Addition Details" itemsUrl="/api/stocks/general" closeUrl="/school/stocks/general/addition" match={this.props.match} history={this.props.history} location={this.props.location} />
    }
}
