import React, { Component } from 'react';
import axios from 'axios';

import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class StockAddition extends Component {
    constructor(props) {
        super(props);
        window.stockAdditionActionEdit = this.edit;
        const self = this;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get(self.props.url, {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'name', name: 'name', 'class': 'text-center'},
                { data: 'quantity', name: 'quantity', 'class': 'text-center', render: (data,type,row)=>( data + ' ' + (row['unit'] || '') )},
                { data: 'date', name: 'date', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="stockAdditionActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    edit = (addition) => {
        this.props.history.push(`${this.props.match.url}/${addition}`);
    }

    render() {
        let match = this.props.match;
        return <div>
            <DataTable ref={this.dataTable} title={this.props.title} config={this.dataTableConfig}>
                <th>Item Name</th>
                <th>Quantity</th>
                <th>Date</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
        </div>

    }
}