import React, { Component } from 'react';
import axios from 'axios';

import StockForm from './StockForm';
import DataTable from './DataTable';
import getCookie from '../utils/getCookie';

export default class StockItems extends Component {
    constructor(props) {
        super(props);
        window.stockItemActionEdit = this.edit;
        const self = this;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get(self.props.url, {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'name', name: 'name', 'class': 'text-center'},
                { data: 'quantity', name: 'quantity', 'class': 'text-center', render: (data,type,row)=> ( data ? (data + ' ' + (row['unit'] || '')) : '-' ) },
                { data: 'used', name: 'used', 'class': 'text-center', render: (data,type,row)=> ( data ? (data + ' ' + (row['unit'] || '')) : '-' ) },
                { data: 'added', name: 'added', 'class': 'text-center', render: (data,type,row)=> ( data ? (data + ' ' + (row['unit'] || '')) : '-' ) },
                { data: 'remainder', name: 'remainder', 'class': 'text-center', render: (data,type,row)=> ( data ? (data + ' ' + (row['unit'] || '')) : '-' ) },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="stockItemActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    edit = (item) => {
        this.props.history.push(`${this.props.match.url}/${item}`);
    }

	render() {
		let match = this.props.match;
		return <div>
            <DataTable ref={this.dataTable} title={this.props.title} config={this.dataTableConfig}>
                <th>Item Name</th>
                <th>Quantity</th>
                <th>Used</th>
                <th>Added</th>
                <th>Remainding</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}
