import React, { Component } from 'react';
import { Route, Link } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import StockItems from '../components/StockItems';
import StockNew from '../components/StockNew';
import StockEdit from '../components/StockEdit';
import StockUsage from '../components/StockUsage';
import StockUsageNew from '../components/StockUsageNew';
import StockUsageEdit from '../components/StockUsageEdit';
import StockAddition from '../components/StockAddition';
import StockAdditionNew from '../components/StockAdditionNew';
import StockAdditionEdit from '../components/StockAdditionEdit';

export default class StockPagesLaboratory extends Component {
    render() {
        let match = this.props.match;
        return <div className="container-fluid">
            <div className="row">
                <div className="col-md-12">
                  <span className="pull-right">
                  <Link to={`${match.url}/addition`} className="btn btn-secondary details ml-1">View Additions</Link>
                  <Link to={`${match.url}/usage`} className="btn btn-warning details ml-1">View Usage</Link>
                  <Link to={`${match.url}/addition/new`} className="btn btn-primary details ml-1">Enter Addition</Link>
                  <Link to={`${match.url}/usage/new`} className="btn btn-info details ml-1">Enter Usage</Link>
                    <Link to={`${match.url}/new`} className="btn btn-success details ml-1">Add Item</Link>
                    <Link to={`${match.url}`} className="btn btn-dark details ml-1">Laboratory Stock</Link>
                  </span>
                </div>
            </div>
            <AnimatedSwitch debug={true}>
                        <Route path={`${match.path}/new`} render={(props) => <StockNewLaboratory {...props} /> } />
                        <Route path={`${match.path}/usage/new`} render={(props) => <StockUsageNewLaboratory {...props} /> } />
                        <Route path={`${match.path}/usage/:usageId`} render={(props) => <StockUsageEditLaboratory {...props} /> } />
                        <Route path={`${match.path}/usage`} render={(props) => <StockUsageLaboratory {...props} /> } />
                        <Route path={`${match.path}/addition/new`} render={(props) => <StockAdditionNewLaboratory {...props} /> } />
                        <Route path={`${match.path}/addition/:additionId`} render={(props) => <StockAdditionEditLaboratory {...props} /> } />
                        <Route path={`${match.path}/addition`} render={(props) => <StockAdditionLaboratory {...props} /> } />
                        <Route path={`${match.path}/:itemId`} render={(props) => <StockEditLaboratory {...props} /> } />
                        <Route path={`${match.path}`} render={(props) => <StockLaboratory {...props} /> } />
                        <Route path=""><h1>404 Not Found</h1></Route>
                    </AnimatedSwitch>
          </div>
    }
}

export class StockLaboratory extends Component {
    render() {
        return <StockItems title="Laboratory Stock" url="/api/stocks/laboratory" match={this.props.match} history={this.props.history} />
    }
}

export class StockNewLaboratory extends Component {
    render() {
        return <StockNew url="/api/stocks/laboratory" closeUrl="/school/stocks/laboratory" history={this.props.history} />
    }
}

export class StockEditLaboratory extends Component {
    render() {
        return <StockEdit closeUrl="/school/stocks/laboratory" match={this.props.match} history={this.props.history} location={this.props.location} />
    }
}

export class StockUsageLaboratory extends Component {
    render() {
        return <StockUsage url="/api/stocks/laboratory/usage" title="Laboratory Usage" match={this.props.match} history={this.props.history} />
    }
}


export class StockUsageNewLaboratory extends Component {
    render() {
        return <StockUsageNew title="Usage Details" itemsUrl="/api/stocks/laboratory" closeUrl="/school/stocks/laboratory" history={this.props.history} />
    }
}

export class StockUsageEditLaboratory extends Component {
    render() {
        return <StockUsageEdit title="Usage Details" itemsUrl="/api/stocks/laboratory" closeUrl="/school/stocks/laboratory/usage" match={this.props.match} history={this.props.history} location={this.props.history} />
    }
}

export class StockAdditionLaboratory extends Component {
    render() {
        return <StockAddition url="/api/stocks/laboratory/addition" title="Laboratory Addition" match={this.props.match} history={this.props.history} />
    }
}

export class StockAdditionNewLaboratory extends Component {
    render() { 
        return <StockAdditionNew title="Addition Details" itemsUrl="/api/stocks/laboratory" closeUrl="/school/stocks/laboratory" history={this.props.history} />
    }
}


export class StockAdditionEditLaboratory extends Component {
    render() {
        return <StockAdditionEdit title="Addition Details" itemsUrl="/api/stocks/laboratory" closeUrl="/school/stocks/laboratory/addition" match={this.props.match} history={this.props.history} location={this.props.location} />
    }
}
