import React, { Component } from 'react';
import { Route, Link } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import StockItems from '../components/StockItems';
import StockNew from '../components/StockNew';
import StockEdit from '../components/StockEdit';
import StockUsage from '../components/StockUsage';
import StockUsageNew from '../components/StockUsageNew';
import StockUsageEdit from '../components/StockUsageEdit';
import StockAddition from '../components/StockAddition';
import StockAdditionNew from '../components/StockAdditionNew';
import StockAdditionEdit from '../components/StockAdditionEdit';

export default class StockPagesKitchen extends Component {
    render() {
        let match = this.props.match;
        return <div className="container-fluid">
            <div className="row">
                <div className="col-md-12">
                  <span className="pull-right">
                  <Link to={`${match.url}/addition`} className="btn btn-secondary details ml-1">View Additions</Link>
                  <Link to={`${match.url}/usage`} className="btn btn-warning details ml-1">View Usage</Link>
                  <Link to={`${match.url}/addition/new`} className="btn btn-primary details ml-1">Enter Addition</Link>
                  <Link to={`${match.url}/usage/new`} className="btn btn-info details ml-1">Enter Usage</Link>
                    <Link to={`${match.url}/new`} className="btn btn-success details ml-1">Add Item</Link>
                    <Link to={`${match.url}`} className="btn btn-dark details ml-1">Kitchen Stock</Link>
                  </span>
                </div>
            </div>
            <AnimatedSwitch debug={true}>
                        <Route path={`${match.path}/new`} render={(props) => <StockNewKitchen {...props} /> } />
                        <Route path={`${match.path}/usage/new`} render={(props) => <StockUsageNewKitchen {...props} /> } />
                        <Route path={`${match.path}/usage/:usageId`} render={(props) => <StockUsageEditKitchen {...props} /> } />
                        <Route path={`${match.path}/usage`} render={(props) => <StockUsageKitchen {...props} /> } />
                        <Route path={`${match.path}/addition/new`} render={(props) => <StockAdditionNewKitchen {...props} /> } />
                        <Route path={`${match.path}/addition/:additionId`} render={(props) => <StockAdditionEditKitchen {...props} /> } />
                        <Route path={`${match.path}/addition`} render={(props) => <StockAdditionKitchen {...props} /> } />
                        <Route path={`${match.path}/:itemId`} render={(props) => <StockEditKitchen {...props} /> } />
                        <Route path={`${match.path}`} render={(props) => <StockKitchen {...props} /> } />
                        <Route path=""><h1>404 Not Found</h1></Route>
                    </AnimatedSwitch>
          </div>
    }
}

export class StockKitchen extends Component {
    render() {
        return <StockItems title="Kitchen Stock" url="/api/stocks/kitchen" match={this.props.match} history={this.props.history} />
    }
}

export class StockNewKitchen extends Component {
    render() {
        return <StockNew url="/api/stocks/kitchen" closeUrl="/school/stocks/kitchen" history={this.props.history} />
    }
}

export class StockEditKitchen extends Component {
    render() {
        return <StockEdit closeUrl="/school/stocks/kitchen" match={this.props.match} history={this.props.history} location={this.props.location} />
    }
}

export class StockUsageKitchen extends Component {
    render() {
        return <StockUsage url="/api/stocks/kitchen/usage" title="Kitchen Usage" match={this.props.match} history={this.props.history} />
    }
}


export class StockUsageNewKitchen extends Component {
    render() {
        return <StockUsageNew title="Usage Details" itemsUrl="/api/stocks/kitchen" closeUrl="/school/stocks/kitchen" history={this.props.history} />
    }
}

export class StockUsageEditKitchen extends Component {
    render() {
        return <StockUsageEdit title="Usage Details" itemsUrl="/api/stocks/kitchen" closeUrl="/school/stocks/kitchen/usage" match={this.props.match} history={this.props.history} location={this.props.history} />
    }
}

export class StockAdditionKitchen extends Component {
    render() {
        return <StockAddition url="/api/stocks/kitchen/addition" title="Kitchen Addition" match={this.props.match} history={this.props.history} />
    }
}

export class StockAdditionNewKitchen extends Component {
    render() { 
        return <StockAdditionNew title="Addition Details" itemsUrl="/api/stocks/kitchen" closeUrl="/school/stocks/kitchen" history={this.props.history} />
    }
}


export class StockAdditionEditKitchen extends Component {
    render() {
        return <StockAdditionEdit title="Addition Details" itemsUrl="/api/stocks/kitchen" closeUrl="/school/stocks/kitchen/addition" match={this.props.match} history={this.props.history} location={this.props.location} />
    }
}
